import '../../assets/style/ani-txt-card.scss'

import React from 'react'
import Lottie from 'react-lottie'

export interface IAniTxtCardProps {
    image: string
    lottie: any
    title: string
    description: string[]
    aniVideo: string
    teachVideo?: string
}

interface IAniTxtCardState {
    itemW: number
    itemH: number
    isPaused: boolean
}

export default class AniTxtCard extends React.Component<IAniTxtCardProps, IAniTxtCardState> {
    constructor(props: any) {
        super(props);
        this.state = {
            itemW: 210,
            itemH: 140,
            isPaused: true
        }
    }

    handleResize = () => {
        let containerW: number = 0, container: HTMLElement | null = document.getElementById('galleryList');
        if (typeof container !== 'undefined' && container !== null) {
            containerW = container.clientWidth;
        }
        this.setState({
            itemW: containerW * 0.3166,
            itemH: containerW * 0.2153,
        })
    }

    componentDidMount() {
        this.handleResize();
        window.addEventListener('resize', this.handleResize);
    }

    componentWillUnmount() {
        window.removeEventListener('resize', this.handleResize);
    }

    render() {
        const mouseenterItem = () => {
            this.setState({ isPaused: false })
        }
        const mouseleaveItem = () => {
            this.setState({ isPaused: !this.state.isPaused })
        }
        const defaultOptions = {
            loop: true,
            autoplay: false,
            animationData: this.props.lottie,
            rendererSettings: {
                preserveAspectRatio: 'xMidYMid slice'
            }
        };
        return (
            <div className='rect-ani-card' onMouseEnter={mouseenterItem} onMouseLeave={mouseleaveItem}>
                <div className='card-img' style={{ backgroundImage: 'url(' + this.props.image }}>
                    <Lottie options={defaultOptions}
                        height={this.state.itemH}
                        width={this.state.itemW}
                        isPaused={this.state.isPaused} />
                </div>
                <div className='card-text'>
                    <span>{this.props.title}</span>
                </div>
            </div>
        );
    }
}
